export default function MatchCard({ match, isLive }: any) {
  return (
    <div className="flex items-center justify-between">
      {/* Home Team */}
      <div className="flex-1 text-center">
        <div className="w-12 h-12 bg-muted rounded-full flex items-center justify-center mx-auto mb-3 font-bold text-lg">
          {match.homeTeam
            .split(" ")
            .map((w: string) => w[0])
            .join("")}
        </div>
        <p className="font-bold text-foreground text-sm">{match.homeTeam}</p>
      </div>

      {/* Score/Time */}
      <div className="flex-1 text-center px-6">
        <div className={`text-4xl font-black mb-2 ${isLive ? "text-primary" : "text-foreground"}`}>
          {match.homeScore} - {match.awayScore}
        </div>
        <p className="text-sm text-muted-foreground">{isLive ? `${match.minute}'` : "FT"}</p>
      </div>

      {/* Away Team */}
      <div className="flex-1 text-center">
        <div className="w-12 h-12 bg-muted rounded-full flex items-center justify-center mx-auto mb-3 font-bold text-lg">
          {match.awayTeam
            .split(" ")
            .map((w: string) => w[0])
            .join("")}
        </div>
        <p className="font-bold text-foreground text-sm">{match.awayTeam}</p>
      </div>
    </div>
  )
}
